adas a cabo a fin de crear efecto social y medioambiental positivo, medible junto con retorno financiero, contribuyendo al desarrollo social y protección de medio ambiente. Además de abordar problemas como lo que refiere Ship2BFundation (2021) "),_c('span',{staticClass:"tc1"},[_c('strong',[_vm._v("“la inversión de impacto aborda problemas ambientales y sociales diferentes como la agricultura sostenible, vivienda asequible, salud asequible y accesible, desafíos del cambio climático y otros temas importantes que mejoren la calidad de vida entre la población”.")])]),_vm._v(" Este tipo de inversión está potenciando las ganas de los inversores por proyectos innovadores de alto impacto.")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bloque-texto-g__texto p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("La inversión de impacto también es una ganancia financiera ya que los costos sociales van de la mano con los beneficios financieros. Una indagación de un rendimiento financiero no califica una inversión social en una donación caritativa o una donación no reembolsable. Lo que distingue a la inversión social de la inversión tradicional es que la rentabilidad financiera está estrechamente relacionada con el desempeño social y depende del logro de los objetivos sociales: específicamente, los inversores solo recibirán productividad y rentabilidad financiera si esto se logra a eso se le puede llamar mínimo impacto social.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mt-4"},[_c('div',{staticClass:"col-lg-10",attrs:{"data-aos":"zoom-in-down"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img7.svg"),"alt":"Texto que describa la imagen"}}),_c('figcaption',[_vm._v("Imagen realizada en base a Bustamante (2016).")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"r